<template>
    <v-container class="pa-0" style="max-width: 100%; height: 100%;">

        <div v-if="dataReady">
            <router-view style="padding-bottom: 64px"></router-view>
            <v-bottom-navigation
                    style="z-index: 4 !important"
                    height="64px"
                    app
                    :value="bottomNav"
                    color="transparent"
            >
                <v-row style="height: 100%; max-width: 100vw">
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                align="center"
                                value="ranking"
                                @click="redirectTo('rankingHome')"
                        >
                            <!--<span>Ranking</span>-->
                            <v-icon>mdi-crown</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                value="events"
                                @click="redirectTo('eventsHome')"
                        >
                            <!--<span>Eventos</span>-->
                            <v-icon>mdi-map-marker</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                value="search"
                                @click="redirectTo('searchHome')"
                        >
                            <!--<span>Explorar</span>-->
                            <v-icon>mdi-magnify</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                value="chats"
                                @click="redirectTo('chatHome')"
                        >
                            <!--<span>Chats</span>-->
                            <v-icon>mdi-message</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                value="profile"
                                @click="redirectTo('profileHome')"
                        >
                            <!--<span>Perfil</span>-->
                            <v-icon>mdi-account</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>


            </v-bottom-navigation>
        </div>
        <div id="loading" v-if="!dataReady">
            <v-progress-circular
                    :size="70"
                    :width="7"
                    color="primary"
                    indeterminate
            ></v-progress-circular>
        </div>
    </v-container>


</template>

<script>
    import {mapActions, mapGetters} from "vuex";

    export default {
        name: "Dashboard",
        data() {
            return {
                bottomNav: this.$route.meta.menu,
                dataReady: true
            }
        },
        methods: {
            ...mapActions(['initAppInfo', 'getLoggedUser']),
            ...mapActions('permissions', ['checkForGeolocationPermission', 'checkForPushNotificationsPermission']),

            redirectTo: function (goal) {
                if (!goal.startsWith(this.$route.meta.menu)) {
                    this.$router.push({name: goal});
                }

            }

        },
        mounted() {
            this.bottomNav = this.$route.meta.menu;
            /*let self = this;
            let resultInitAppInfo = self.initAppInfo();

            resultInitAppInfo
                .then(() => {
                    self.dataReady = true;
                })
                .catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                });*/

        },
        created() {
            let self = this;
            self.getLoggedUser().then(async currentUser => {
                if (currentUser != null && currentUser != undefined) {
                    // user is logged in
                    await self.checkForPushNotificationsPermission()
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                    await self.checkForGeolocationPermission()
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }
            });
        }
    }
</script>

<style scoped>
    .bottom-nav-button {
        padding: 0 !important;
        width: 100% !important;
        height: 100% !important;
        max-width: unset !important;
        min-width: unset !important;
    }
</style>
